import React, { Fragment } from "react";
import { Card, Avatar } from 'antd';
import galleryallAvatar from '../../resources/images/showcase-galleryall-avatar.png'
import officeReservationAvatar from '../../resources/images/showcase-office-reservation-avatar.png'
import './Showcase.css'

const { Meta } = Card;

const cardForProjGallery = 
<Card
  className="projectCard"
  style={{ 
    width: '500px', 
    marginTop: 16, 
    background:"rgb(43,43,43)",
  }}
  key="1"
>
  <Meta
    className="projectCardMeta"
    title={(<strong>GalleryAll</strong>)}
    description={(
      <Fragment>
        <p>A React application to exhibit the works of the gallery</p>
        <p><strong>Technologies used:</strong><br/>
          React.js <br/>
          Ant Design<br/>
          React-router <br/>
          Styled-component
        </p>
        <p><a href="https://gist.github.com/dev-hbc/a51bbb8f761432450a604e59dfb4b0d5" target="https://gist.github.com/dev-hbc/a51bbb8f761432450a604e59dfb4b0d5">Detailed Description</a></p>
        <p><a href="https://galleryall.com" target="https://galleryall.com">GalleryAll.com</a></p>
      </Fragment>
    )}
    avatar={
      <Avatar 
        src={galleryallAvatar} 
        shape="square"
        style={{width: 300, height: 300, border: "1px solid grey"}}
      />
    }
  >
  </Meta>
</Card>;

const cardForOfficeReservation = 
<Card
  className="projectCard"
  style={{ 
    width: '600px', 
    marginTop: 16, 
    background:"rgb(43,43,43)",
  }}
  key="2"
>
  <Meta
    className="projectCardMeta"
    title={(<strong>OfficeReservation</strong>)}
    description={(
      <Fragment>
        <p>An application to offer office reservation service</p>
        <p><strong>Technologies used:</strong><br/>
          Spring Boot <br/>
          Spring Data JPA <br/>
          MySQL <br/>
          WeChat Miniprogram <br/>
          AWS Services: EC2, RDS for MySQL
        </p>
        <p><a href="https://youtu.be/WQnehw8guRA" target="https://youtu.be/WQnehw8guRA">Project Demo</a></p>
      </Fragment>
    )}
    avatar={
      <Avatar 
        src={officeReservationAvatar} 
        shape="square"
        style={{width: 300, height: 500, border: "1px solid grey"}}
      />
    }
  >
  </Meta>
</Card>;

const Showcase = _ => {

    const projects = [cardForProjGallery, cardForOfficeReservation]
    
    return (
      <section id="portfolio">
        <div className="row">
          <div className="twelve columns collapsed">
            <h1>Check Out Some of My Works.</h1>

            <div
              id="portfolio-wrapper"
              className="bgrid-quarters s-bgrid-thirds cf"
            >
              {projects}
            </div>
          </div>
        </div>
      </section>
    );
}

export default Showcase;