import React from "react";
import profilepic_m1 from "../../resources/images/profilepic-m1.jpg"

const About = _ => {
    return (
        <section id="about">

            <div className="row">

                <div className="three columns">
                    <img className="profile-pic" src={profilepic_m1} alt=""/>
                </div>
                <div className="nine columns main-col">
                    <h2>About Me</h2>
                    <p>
                        Currently I focus on Frontend technology stack of React and React Native which has been used in developing Android, IOS applications, and Wechat Miniprogram.
                    </p>

                    <div className="row">
                        <div className="columns contact-details">
                            <h2>Contact Details</h2>
                            <p className="address">
                                    <span>Hongbin Cai</span><br/>
                                    <span>Burneby, Vancouver, British Columbia, Canada
                                    </span><br/>
                                    <span>(514) 431-0311</span><br/>
                            </p>
                        </div>
                        <div className="columns download">
                            <p>
                                <a href="https://drive.google.com/file/d/1ywgP-WqJOS-RhPCksI58ho1GJOFobQEc/view?usp=sharing" className="button"><i className="fa fa-download"></i>Download Resume</a>
                            </p>
                        </div>
                    </div>
                    
                </div> 
            </div>
        </section>
    );
}

export default About;