import React from "react";

const Banner = _ => {

    const responsiveHeadlineStyle = {
        fontSize: "86.5px"
    }
    return (
        <div className="row banner">
            <div className="banner-text">
                <h1 className="responsive-headline" style={responsiveHeadlineStyle}>
                    I'm Hongbin Cai.
                </h1>
                <h3>I'm a <span>Software Engineer</span> living in Vancouver, who is a codie, foodie, and hikie, passionate with <span>Fullstack</span> technologies, want to try every different program, every different trail, and every different recipe. 
                Embracing the new things can always make me a day. Let's <a className="smoothscroll" href="#about">start scrolling</a> and learn more <a className="smoothscroll" href="#about">about me</a>.</h3>
                <hr/>
                <ul className="social">
                    <li><a href="https://www.linkedin.com/in/hongbin-c-57437012a/"><i className="fa fa-linkedin"></i></a></li>
                    <li><a href="https://github.com/dev-hbc"><i className="fa fa-github"></i></a></li>
                </ul>
            </div>
        </div>
    );
}

export default Banner;